import { HeadNav } from "layout/"

export default {
  name: "build",
  data() {
    return {
      /**/
      listData: [],
      fieldList: [
        {label: "URL", key: "source"},
        {label: "", key: "target"},
        {label: "", key: "pathRewrite"}
      ],
      btnInfo: {
        width: 134,
        batch: false,
        select: false,
        update_text: "",
        delete_text: ""
      },

      /*dialog*/
      dialogVisible: false,
      dialogTitle: "",
      setting: {
        labelWidth: 72
      },
      rules: {
        source: [{ required: true, message: "URL", trigger: 'blur' }],
        target: [{ required: true, message: "", trigger: 'blur' }],
        pathRewrite: [{ required: true, message: "", trigger: 'blur' }]
      },
      defaultValue: {}

    }
  },
  components: {
    HeadNav
  },
  methods: {
    init() {
      var token = this.$store.state.user.userinfo.token;
      if(token == "login"){
        this.getProxyList();
      }else{
        this.$router.push('/login');
      }
    },

    /**/
    getProxyList() {
      this.$$api_proxy_getProxyList({
        defFn: data=>{
          this.listData = data;
        }
      });
    },

    /**/
    onListAddBtn() {
      this.dialogVisible = true;
      this.dialogTitle = "";
      this.resForm();
      this.defaultValue = {id: -1};
    },

    /**/
    onListUpdateBtn(option) {
      this.dialogVisible = true;
      this.dialogTitle = "";
      this.resForm();
      var row = {
        id: option.dataIndex
      };
      this.defaultValue = this.$$$.extend(row,option.data);
    },

    /**/
    onCancel() {
      this.dialogVisible = false;
    },

    /*proxy*/
    createOrUpdateProxyItem() {
      this.$$api_proxy_createOrUpdateProxyItem({
        data: this.defaultValue,
        fn: data=>{
          this.listData = data;
          this.onCancel();
        }
      });
    },

    /*proxy*/
    deleteProxyItem(option) {
      this.$alert('', '', {
				confirmButtonText: '',
				type: "warning",
				callback: action => {
          if(action == "cancel") return;
          this._deleteProxyItem(option.dataIndex);
        }
      });
    },
    _deleteProxyItem(id) {
      this.$$api_proxy_deleteProxyItem({
        data: {id},
        fn: data=>{
          this.listData = data;
        }
      });
    },

    /*form*/
    resForm() {
      this.$refs['form-data'] && this.$refs['form-data'].resetFields();
    }
  },
  beforeMount() {
    this.init();
  }
}


