var env = process.env;

var gbs={
	host: '/build', //
	db_prefix: 'build_', //key

	//
	api_status_key_field:'status',
	//value
	api_status_value_field:"200",

	api_data_field:'data',

	api_custom:{
		404:function (res) {
			this.$store.dispatch('remove_userinfo').then(() => {
				this.$alert(res.status + ',' + res.msg + '', '', {
					confirmButtonText: '',
					callback: action => {
						this.$router.push('/login');
					}
				});
			});
		}
	}
};

var cbs={
	/**
	 * ajax200
	 * @param  {object} err 
	 */
	statusError(err) {
		if (err.status !== "404") {

			var msg = err.msg || err.data.exMessage || "";
			this.$alert(''+err.status+',:' + msg, '', {
			  confirmButtonText: '',
			  type: 'warning'
		    });

		} else {
			this.$store.dispatch('remove_userinfo').then(() => {
				this.$alert(err.status + ',' + err.msg + '', '', {
					confirmButtonText: '',
					callback: action => {
						this.$router.push('/login');
					}
				});
			});
		}
	},

	/**
	 * ajax
	 */
	requestError(err) {

		this.$message({
			showClose: true,
			message: '' + (err.response ? err.response.status : '') + ',' + (err.response ? err.response.statusText : ''),
			type: 'error'
		});
	}

};

export {
	gbs,
	cbs
};



